.wrap {
  display: flex;
  gap: 16px;
}

button {
  width: 120px;
  font-size: 16px;
  font-weight: 700;
  text-transform: capitalize;
  cursor: pointer;
  padding: 5px 10px;
  border-radius: 4px;
  border: 1px solid gray;
}

button {
  box-shadow: 0 5px 7px -1px rgba(51, 51, 51, 0.23);
}

.good:hover {
  background-color: var(--good-color);
}

.neutral:hover {
  background-color: var(--neutral-color);
}

.bad:hover {
  background-color: var(--bad-color);
}
